import React from 'react'
import BreadcrumContent from '../../../../Global/Pages/BreadcrumbContent.component'
import {graphql, useStaticQuery} from 'gatsby'
import ReactSelect from '../../../../Global/App/ReactSelect/ReactSelect'
import { Country } from '../../../../Global/App/ReactSelect/SelectData'


const ShopCheckoutDetails=()=>{
    const data = useStaticQuery(graphql`
query{
    payment: file(relativePath: {eq: "resource/payment.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
    }}`)
    return(
        <section className="shop-checkouts bgc-f7" >
            <div className="container">
                <div className="row">
                    <div className="col-xl-6">
                        <BreadcrumContent title='Checkout' active='Checkout'/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12 col-lg-7 col-xl-7">
                        <div className="checkout_form style2">
                            <h4 className="mb40">Billing Details</h4>
                            <div className="heading text-center">
                                <p>Have a coupon? Click here to enter your code</p>
                            </div>
                            <div className="checkout_coupon ui_kit_button">
                                <form className="form-inline form1">
                                    <input className="form-control coupon_input mr-sm-4" type="search" placeholder="Coupon Code" aria-label="Search"/>
                                    <button type="button" className="btn">Apply Coupon</button>
                                </form>
                                <form className="form2" id="coupon_form" name="contact_form" noValidate="novalidate">
                                    <div className="row">
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputNameZ">First name *</label>
                                                <input id="exampleInputNameZ" name="exampleInputNameZ" className="form-control" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputNameW2">Last name *</label>
                                                <input id="exampleInputNameW2" name="exampleInputNameW2" className="form-control" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-12">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputNameQ3">Company name (optional)</label>
                                                <input id="exampleInputNameQ3" name="exampleInputNameQ3" className="form-control" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-lg-12">
                                            <div className="my_profile_select_box form-group">
                                                <label>Country *</label><br/>
                                                <ReactSelect data={Country}/>
                                            </div>
                                        </div>
                                        <div className="col-sm-12">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputName3a">Street address *</label>
                                                <input id="exampleInputName3a" name="exampleInputName3" className="form-control mb10" placeholder="House number and street name" required="required" type="text"/>
                                                <input id="form_name533s" name="form_name5" className="form-control" placeholder="Apartment, suite, unit etc. (optional)" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputName234">Postcode / ZIP *</label>
                                                <input id="exampleInputName234" name="exampleInputName234" className="form-control" required="required" type="number"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputName512F">Town / City *</label>
                                                <input id="exampleInputName512F" name="exampleInputName512F" className="form-control" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputProvince33443">Province *</label>
                                                <input id="exampleInputProvince33443" name="exampleInputProvince33443" className="form-control" required="required" type="text"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputPhonerds">Phone *</label>
                                                <input id="exampleInputPhonerds" name="exampleInputPhonerds" className="form-control" required="required" type="number"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-12">
                                            <div className="form-group">
                                                <label htmlFor="exampleInputEmailseee">Your Email</label>
                                                <input id="exampleInputEmailseee" name="exampleInputEmailseee" className="form-control required email" required="required" type="email"/>
                                            </div>
                                        </div>
                                        <div className="col-sm-12">
                                            <div className="form-group mb0">
                                                <label className="ai_title" htmlFor="exampleInputTdddextArea">Additional Information</label>
                                                <p>Order notes (optional)</p>
                                                <textarea id="exampleInputTdddextArea" name="exampleInputTdddextArea" className="form-control required" rows="12" placeholder="Notes about your order, e.g. special notes for delivery." required="required"></textarea>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-xl-5">
                        <div className="order_sidebar_widget mb30">
                            <h4 className="title">Your Order</h4>
                            <ul>
                                <li><p>Album  × 1 <span className="float-right">$12,00</span></p></li>
                                <li><p>Beanie x 5 <span className="float-right">$45,56</span></p></li>
                                <li className="subtitle"><p>Subtotal <span className="float-right">$13,000</span></p></li>
                                <li className="subtitle"><p>Total <span className="float-right totals color-orose">$943,00</span></p></li>
                            </ul>
                        </div>
                        <div className="payment_widget">
                            <div className="ui_kit_checkbox style2">
                                <h4 className="mb20">Payment</h4>
                                <div className="custom-control custom-checkbox">
                                    <input type="checkbox" className="custom-control-input" id="customCheck80"/>
                                    <label className="custom-control-label" htmlFor="customCheck80">Direct bank transfer</label>
                                </div>
                                <div className="bt_details">
                                    <p>Make your payment directly into our bank account. Please use your Order ID as the payment reference.Your order will not be shipped until the funds have cleared in our account.</p>
                                </div>
                                <div className="custom-control custom-checkbox">
                                    <input type="checkbox" className="custom-control-input" id="customCheck82"/>
                                    <label className="custom-control-label" htmlFor="customCheck82">Check payments</label>
                                </div>
                                <div className="custom-control custom-checkbox">
                                    <input type="checkbox" className="custom-control-input" id="customCheck83"/>
                                    <label className="custom-control-label" htmlFor="customCheck83">Cash on delivery</label>
                                </div>
                                <div className="custom-control custom-checkbox">
                                    <input type="checkbox" className="custom-control-input" id="customCheck84"/>
                                    <label className="custom-control-label" htmlFor="customCheck84">PayPal <img className="pr15" src={data.payment.childImageSharp.fluid.originalImg} alt="payment.png"/></label>
                                </div>
                            </div>
                        </div>
                        <div className="ui_kit_button payment_widget_btn">
                            <button type="button" className="btn dbxshad btn-lg btn-thm btn-block">Place Order</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
export default ShopCheckoutDetails