import React from 'react'
import Select ,{ components } from   "react-select";
import styled from "styled-components";


const ShowTick = styled.span`
    display: flex;
    right: 15px;
    top: 5px;
    &:after {
        content: "";
        display: flex;
        width: 0.5em;
        height: 1em;
        border-style: solid;
        color: ${props => props.Color || '#484848'};
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
    }`
    const newStyle = {
    display: "flex",
    justifyContent: "space-between"
};
const DropdownIndicatorIcon=styled.span `
        &:after {
        display: inline-block;
        margin-left: .255em;
        vertical-align: .255em;
        content: "";
        border-top: .3em solid;
        border-right: .3em solid transparent;
        border-bottom: 0;
        border-left: .3em solid transparent;
    }
`
// const ClientSideOnlyLazy = React.lazy(() =>
//   import ('../ReactSelect/ReactSelect')
// )
// const MyPage = ({data,style2,tickColor}) => {
//   const isSSR = typeof window === "undefined"

//   return (
//     <>
//       {!isSSR && (
//         <React.Suspense fallback={<div />}>
//           <ClientSideOnlyLazy data={data} style2={style2}  tickColor={tickColor}
//             isSearchable={isSearchable}/>
//         </React.Suspense>
//       )}
//     </>
//   )
// }
const ReactSelect=({data,style2,tickColor,isSearchable}) => {
    const isSSR = typeof window === "undefined"
const DropdownIndicator = props => {
  return (
    <components.DropdownIndicator {...props}>
      <DropdownIndicatorIcon/>
    </components.DropdownIndicator>
  );
};
    const Option = (props) => {
    const {
        children,
        className,
        cx,
        getStyles,
        isDisabled,
        isFocused,
        isSelected,
        innerRef,
        innerProps,
        tickColor
    } = props;
    return isSelected ? (
        <div
        ref={innerRef}
        css={getStyles("option", props)}
        className={cx(
            {
            option: true,
            "option--is-disabled": isDisabled,
            "option--is-focused": isFocused,
            "option--is-selected": isSelected
            },
            className
        )}
        {...innerProps}
        style={newStyle}
        >
        {children}
        <ShowTick  Color={tickColor}></ShowTick>
        </div>
    ) : (
        <div
        ref={innerRef}
        css={getStyles("option", props)}
        className={cx(
            {
            option: true,
            "option--is-disabled": isDisabled,
            "option--is-focused": isFocused,
            "option--is-selected": isSelected
            },
            className
        )}
        {...innerProps}
        >
        {children}
        </div>
    );
    };
    const customStyles={
        container:(base)=>({
            ...base,
            color: '#484848',
            '&:hover': {
            color: '#7f7f7f',
            }
        }),
        option: (base) => ({
          ...base,
          height: "100%",
          paddingLeft: "20px",
          color: '#484848',
          zIndex:'333232'
        }),
        control:(base)=>({
            ...base,
            borderColor: 'rgb(235, 235, 235)',
            '&:hover': {
            borderColor: 'rgb(235, 235, 235)',
            }
        }),
        dropdownIndicator:(base)=>({
            ...base,
            marginRight:'10px',
            color:'',
            '&:hover': {
            color: '',
            }
        }),
        indicatorsContainer:(base)=>({
            ...base,
            color:'',
            '&:hover': {
            color: '',
            }
        }),
        singleValue:(base,state)=>({
            ...base,
            color: state.selectProps.style2 ? '#006c70' : ''
            // color: '#484848',
            // '&:hover': {
            // color: '#7f7f7f',
            // }
        })
      }
    return(
  <div>
  {!isSSR && (
    <Select
      isSearchable={isSearchable ? true : false}
      classNamePrefix="react-select"
      closeMenuOnSelect={true}
      components={{ Option, IndicatorSeparator: () => null, DropdownIndicator }}
      styles={customStyles}
      defaultValue={data[0]}
      options={data}
      theme={(theme) => ({
        ...theme,
        borderRadius: 8,
        border: "1px solid white",
        colors: {
          ...theme.colors,
          primary: "#f7f7f7",
          primary50: "#f7f7f7",
          primary25: "#f7f7f7",
          danger: "#f7f7f7"
        }
      })
      }
      style2={style2}
      tickColor={tickColor}
    />
  )}
    
  </div>
)
}

export default ReactSelect